.tree-panel-wrapper {
  scroll-behavior: smooth;

  // Ensure smooth transitions for tree nodes
  :host ::ng-deep .p-tree {
    .p-treenode {
      transition: background-color 0.2s ease;
    }
  }

  // Node search loading overlay styles
  .node-search-overlay {
    backdrop-filter: blur(1px);
    animation: fadeIn 0.2s ease-in;
  }

  .node-search-message {
    box-shadow:
      0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    animation: slideIn 0.3s ease-out;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes slideIn {
    from {
      transform: translateY(-10px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
